.ana-react-chat .messages-list {
  flex: 1;
  display: flex;
  flex-flow: column;
  position: relative;
  overflow: hidden;
}
.ana-react-chat .messages-list.loading {
  width: 100%;
  height: 100%;
}
.ana-react-chat .messages-list.empty {
  width: 100%;
  height: 100%;
  display: flex;
  text-align: center;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.ana-react-chat .messages-list.empty svg {
  width: auto;
  height: 64px;
  opacity: 0.5;
}
.ana-react-chat .messages-list.empty span {
  opacity: 0.8;
  display: block;
  margin-top: 15px;
}
.ana-react-chat .messages-list-content {
  display: flex;
  flex-direction: column-reverse;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
  max-height: 100%;
}
.ana-react-chat .messages-list-content ul li:first-child {
  margin-top: 0 !important;
}
.ana-react-chat .messages-list > button {
  border: none;
  right: 20px;
  bottom: 20px;
  display: flex;
  height: 25px;
  width: 25px;
  padding: 0;
  outline: none;
  position: absolute;
  border-radius: 3px;
  align-items: center;
  justify-content: center;
  background-color: #eee;
  transition: all 0.2s ease 0.2s;
  border-radius: 50%;
}
.ana-react-chat .messages-list > button[data-badge]::after {
  content: attr(data-badge);
  position: absolute;
  top: -5px;
  right: -5px;
  width: 15px;
  height: 15px;
  border-radius: 15px;
  font-size: 10px;
}
.ana-react-chat .messages-list > button svg {
  width: auto;
  fill: #999;
  height: 20px;
}
.ana-react-chat:lang(ar) .messages-list > button {
  left: 20px;
  right: auto;
}