@keyframes spining {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.ana-react-chat {
  width: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Noto Naskh Arabic", "Open Sans";
  position: relative;
}
.ana-react-chat * {
  outline: none;
}
.ana-react-chat:lang(ar) {
  direction: rtl;
}